import React, { useState } from "react";
import "./QuestionCard.css";

const QuestionCard = ({
  question,
  questionNumber,
  totalQuestions,
  overallNumber,
  overallTotal,
  onAnswer,
  onSkip,
  onBack,
  onJump,
}) => {
  const [selected, setSelected] = useState("");

  const { text, type, options, section } = question;

  // Likert 5ì  ì²™ë„
  const likert = [
    "ğŸ˜ Strongly Agree",
    "ğŸ™‚ Agree",
    "ğŸ˜ Neutral",
    "ğŸ˜• Disagree",
    "ğŸ˜¡ Strongly Disagree",
  ];

  // ì˜µì…˜ ë³€í™˜ (ë¬¸ìì—´/ë°°ì—´ ëª¨ë‘ ì§€ì›)
  const getOptionsArray = (options) => {
    if (!options) return [];
    if (Array.isArray(options)) return options;

    return options
      .split(/[\|,]/g)
      .map((o) => o.trim())
      .filter((o) => o.length > 0);
  };

  const answerOptions = type === "likert" ? likert : getOptionsArray(options);

  return (
    <div className="question-card fade-in">
      {/* ìƒë‹¨ ì •ë³´ ì˜ì—­ */}
      <div className="q-header">
        <div className="q-section">Section {section}</div>
        <div className="q-progress-group">
          <div className="q-progress-chapter">
            {questionNumber} / {totalQuestions}
          </div>
          <div className="q-progress-overall">
            Overall: {overallNumber} / {overallTotal}
          </div>
        </div>
      </div>

      {/* ì§ˆë¬¸ í…ìŠ¤íŠ¸ */}
      <h2 className="q-text">{text}</h2>

      {/* ê°ê´€ì‹ ì˜µì…˜ (LIKERT / MULTI / SINGLE ëª¨ë‘ ì—¬ê¸°ì—ì„œë§Œ ë Œë”ë§ë¨ â†’ ì¤‘ë³µ ì œê±° ì™„ë£Œ!) */}
      {(type === "multi" || type === "single" || type === "likert") &&
        answerOptions.length > 0 && (
          <div className="q-options">
            {answerOptions.map((opt, idx) => (
              <button
                key={idx}
                className={`q-option ${selected === opt ? "selected" : ""}`}
                onClick={() => setSelected(opt)}
              >
                {opt}
              </button>
            ))}
          </div>
        )}

      {/* ì£¼ê´€ì‹ í…ìŠ¤íŠ¸ íƒ€ì… */}
      {type === "text" && (
        <textarea
          className="q-textarea"
          placeholder="Write your answer..."
          value={selected}
          onChange={(e) => setSelected(e.target.value)}
        />
      )}

      {/* ë²„íŠ¼ ì˜ì—­ */}
      <div className="q-footer">

        {/* Back */}
        <button className="back-btn" onClick={onBack}>
          â† Back
        </button>

        {/* Skip */}
        <button className="skip-btn" onClick={() => onSkip()}>
          â­ Skip
        </button>

        {/* Jump */}
        <button className="jump-btn" onClick={() => onJump()}>
          âŸ Jump
        </button>

        {/* Next */}
        <button
          className={`next-btn ${selected ? "active" : ""}`}
          disabled={!selected}
          onClick={() => {
            if (!selected) return;
            onAnswer(selected);
            setSelected("");
          }}
        >
          Next â†’
        </button>
      </div>
    </div>
  );
};

export default QuestionCard;
